.nav-menu {
	padding-top: 18px;
	position: relative;
	z-index: 9;
	ul {
		display: flex;
		align-items: center;
		li {
			display: flex;
		}
		a {
			color: $whiteO;
			text-decoration: none;
			cursor: pointer;
			display: flex;
			align-items: center;
		}
	}
	&__logo {
		width: 40%;
		a {
			font-size: 14px;
			transition: color 0.4s;
		}
		svg {
			margin-right: 25px;
			use {
				transition: fill 0.4s;
			}
		}
		&:hover {
			a {
				color: $greenL;
			}
			svg {
				use {
					fill: $greenL;
				}
			}
		}
	}
	&__instagram {
		width: 30%;
		display: flex;
		justify-content: center;
		a {
			font-size: 14px;
			transition: color 0.4s;
		}
		svg {
			margin-right: 16px;
			use {
				transition: fill 0.4s;
			}
		}
		&:hover {
			a {
				color: $greenL;
			}
			svg {
				use {
					fill: $greenL;
				}
			}
		}
	}
	&__btn {
		width: 24%;
		display: flex;
		justify-content: flex-end;
		a {
			justify-content: center;
			margin-right: 32px;
		}
	}
	&__phone {
		width: 15%;
		a {
			font-family: $font-ralB;
			font-size: 19px;
			transition: color 0.4s;
			span {
				font-family: $font-ralM;
				font-size: 14px;
			}
		}
		&:hover {
			a {
				color: $greenL;
				use {
					fill: $greenL;
				}
			}
		}
	}
	&__txt-head {
		color: #fefefe;
		font-family: $font-roboto;
		font-size: 13px;
		font-weight: 300;
		transition: color 0.4s;
		&:hover {
			color: $greenL;
		}
		&--none {
			display: none;
		}
	}
	&__txt {
		flex-direction: column;
		font-family: $font-ral;
		font-size: 14px;
		letter-spacing: 1.5px;
		line-height: 1.3;
		display: flex;
		span {
			text-transform: uppercase;
			font-family: $font-ralB;
			font-weight: 700;
			letter-spacing: 1.5px;
		}
	}
}
.nav-menu--fixed {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	background-color: rgba($black, 0.5);
	z-index: 54;
	padding: 19px 0;
	transition: top 0.4s;
	.svg-key-min-logo {
		display: block;
	}
}