.footer {
	background-color: #423232;
	color: $white;
	padding-bottom: 115px;
	padding-top: 275px;
	&__box {
		margin-left: 8.5%;
		display: flex;
		justify-content: flex-end;
	}
	&__wrap {
		display: flex;
		width: calc(100% - 72px);
	}
	&__block {
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		width: 33.3%;
		a {
			transition: color 0.4s;
			svg {
				use {
					transition: fill 0.4s;
				}
			}
			&:hover {
				color: $green;
				svg {
					use {
						fill: $green;
					}
				}
			}
		}
	}
	&__title {
		color: #c8bcbc;
		font-family: $font-ralB;
		font-size: 14px;
		font-weight: 700;
		line-height: 21px;
		letter-spacing: 1.4px;
		margin-bottom: 17px;
	}
	&__par {
		color: #c8bcbc;
		font-family: $font-ral;
		font-size: 13px;
		font-weight: 300;
		line-height: 30px;
		letter-spacing: 1.3px;
		&--insta {
			display: flex;
			align-items: center;
			text-decoration: none;
			margin-bottom: 20px;
			svg {
				margin-right: 20px;
			}
		}
	}
	&__mmit {
		color: #c8bcbc;
		font-family: $font-ral;
		font-size: 12px;
		font-weight: 400;
		line-height: 11.53px;
		text-decoration: none;
		display: flex;
		align-items: center;
		transition: color 0.4s;
		svg {
			margin-right: 8px;
			use {
				transition: fill 0.4s;
			}
		}
	}
}