::selection {color:#fff; background: $green;}
::-moz-selection {color:#fff; background: $green;}
::-webkit-selection {color:#fff; background: $green;}

.container {
	margin-right: auto;
	margin-left: auto;
	padding: 0 30px;
}
@media (min-width: 768px) {
	.container {
		width: 750px;
		padding: 0;
	}
}

@media (min-width: 992px) {
	.container {
		width: 970px;
		padding: 0;
	}
}

@media (min-width: 1280px) {
	.container {
		width: 1280px;
		padding: 0;
	}
}