.document {
	padding-top: 124px;
	padding-bottom: 130px;
	&__box {
		margin-top: 75px;
	}
	&__list {
		display: flex;
		li {
			width: 25%;
			display: flex;
			flex-direction: column;
			align-items: center;
		}
	}
	&__item {
		text-align: center;
		text-decoration: none;
		cursor: pointer;
	}
	&__img	{
		width: 204px;
		height: 271px;
		display: flex;
		justify-content: center;
		align-items: center;
		overflow: hidden;
		img {
			transition: all 0.4s;
			&:hover {
				transform: scale(1.075);
			}
		}
	}
	&__par {
		color: #9b9b9b;
		font-family: $font-ralM;
		font-size: 16px;
		font-weight: 500;
		line-height: 18px;
		letter-spacing: 0.62px;
		min-height: 60px;
		text-align: center;
		margin-bottom: 15px;
	}
	&__btn {
		text-decoration: none;
		padding: 13px 53px 16px 54px;
	}
}