.review {
	padding-top: 79px;
	background: url("../images/green-bg.jpg") center no-repeat;
	-webkit-background-size: cover;
	background-size: cover;
	padding-bottom: 41px;
	&__title {
		color: $white;
	}
	&__slider {
		position: relative;
	}
}
.slider {
	&__item {
		display: flex;
		padding-bottom: 87px;
		outline: none;
	}
	&__img {
	}
	&__text {
		margin-top: 70px;
		margin-left: 74px;
	}
	&__title {
		color: $white;
		font-family: $font-ralB;
		font-size: 24px;
		font-weight: 700;
		line-height: 36px;
		letter-spacing: 1.2px;
		max-width: 467px;
		margin-bottom: 46px;
		position: relative;
		&::before {
			content: '„';
			position: absolute;
			top: -85px;
			right: 32px;
			width: 82px;
			height: 65px;
			opacity: 0.2;
			color: $white;
			font-family: $font-ralB;
			font-size: 250px;
			font-weight: 700;
			text-transform: uppercase;
			letter-spacing: 1.97px;
			z-index: 1;
		}
	}
	&__par {
		max-width: 478px;
		color: $white;
		font-family: $font-ral;
		font-size: 14px;
		font-weight: 400;
		line-height: 24px;
		letter-spacing: 0.49px;
		&:last-child {
			margin-top: 36px;
		}
	}
	&__number {
			position: absolute;
			top: 68.5%;
			left: 51.7%;
			font-family: $font-ral;
			font-size: 14px;
		  color: $white;
	}
	.slick-dots {
		li {
			button {
				width: 11px;
				height: 11px;
				background-color: #ffffff;
				opacity: 0.42;
				border-radius: 50%;
				&::before {
					opacity: 0;
				}
			}
		}
		.slick-active {
			button {
				opacity: 1;
			}
		}
	}
	.slick-prev, .slick-next {
		top: 70%;
		z-index: 7;
	}
	.slick-prev {
		left: 49.5%;
	}
	.slick-next {
		right: 44.5%;
	}
	.slick-prev::before, .slick-next::before {
		font-family: inherit;
	}
	.slick-initialized .slick-slide {
		outline: none;
		&:focus {
			outline: none;
		}
	}
}
.slick-slide {
	&:focus, a {
		outline: none;
	}
}
