.position {
	background: url("../images/position/position-bg.jpg") center no-repeat;
	-webkit-background-size: cover;
	background-size: cover;
	padding-top: 11.6%;;
	padding-bottom: 95px;
	position: relative;
	&::before {
		content: '';
		position: absolute;
		top: -335px;
		right: 0;
		z-index: 0;
		background: url(../images/cloud/cloud-right.png) no-repeat;
		width: 23%;
		height: 100%;
	}
	&::after {
		content: '';
		position: absolute;
		bottom: 0;
		left: 40%;
		z-index: 0;
		background: url(../images/cloud/cloud-bottom.png)0 80px no-repeat;
		width: 60%;
		height: 31%;
	}
	&__cloud {
		position: absolute;
		top: 25%;
		left: 2%;
		z-index: 0;
		background: url(../images/cloud/cloud-little.png) no-repeat;
		width: 30%;
		height: 30%;
	}
	&__box {
		background-color: $white;
		padding: 83px 136px;
		max-width: 536px;
		margin-left: 10%;
		position: relative;
		z-index: 6;

	}
	&__title {
		color: #9b9b9b;
		font-family: $font-ralB;
		font-size: 30px;
		font-weight: 700;
		line-height: 48px;
		text-transform: uppercase;
		letter-spacing: 1.16px;
	}
	&__descr {
		color: #9b9b9b;
		font-family: $font-ralM;
		font-size: 18px;
		font-weight: 500;
		letter-spacing: 0.6px;
		margin-top: 6px;
	}
	&__subtitle {
		color: #423232;
		font-family: $font-ralB;
		font-size: 24px;
		font-weight: 700;
		line-height: 18px;
		letter-spacing: 0.84px;
		margin-top: 72px;
	}
	&__par {
		color: #423232;
		font-family: $font-ral;
		font-size: 14px;
		font-weight: 400;
		letter-spacing: 0.49px;
		margin-top: 20px;
		line-height: 24px;
	}
	&__doc {
		color: #889350;
		font-family: $font-ral;
		font-size: 13px;
		font-weight: 400;
		letter-spacing: 0.46px;
		margin-left: 26px;
		text-decoration: none;
		padding-bottom: 3px;
		transition: color 0.4s;
		span {
			border-bottom: 1px dashed #889350;
			margin-right: 16px;
		}
		&:hover {
			 color: #485219;
			 use {
				 fill: #485219;
			 }
		}
	}
}