.contact {
	padding-top: 80px;
	background-color: #f2eae6;
	padding-bottom: 415px;
	position: relative;
	&__maps {
		position: absolute;
		top: 0;
		left: 0;
		opacity: 0;
		z-index: -1;
		transition: opacity 0.4s;
		width: 100%;
		&--active {
			opacity: 1;
			z-index: 1;
		}
	}
	&__wrapper {
		position: absolute;
		width: 92.5%;
		top: 146px;
		left: 50%;
		transform: translateX(-50%);
	}
	&__box {
		margin-left: 10%;
		margin-right: 10%;
		position: relative;
	}
	&__tabs {
		transform: rotate(-90deg);
		position: absolute;
		left: -154px;
		top: 182px;
		z-index: 6;
	}
	&__tab {
		background-color: $white;
		width: 17px;
		height: 168px;
		color: #a79494;
		font-family: $font-ralB;
		font-size: 14px;
		font-weight: 400;
		letter-spacing: 1.8px;
		padding: 28px 23px;
		margin-left: 6px;
		text-decoration: none;
		cursor: pointer;
		transition: color 0.4s;
		&:hover {
			color: $green;
		}
		&--active {
			color: $green;
		}
	}
	&__wrap {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		color: $black;
		position: relative;
	}
	&__fullscreen {
		text-decoration: none;
		cursor: pointer;
		width: 43px;
		height: 43px;
		position: absolute;
		top: 60px;
		right: 42px;
		z-index: 6;
		background-color: #362a27;
		display: flex;
		justify-content: center;
		align-items: center;
		transition: background-color 0.4s;
		&:hover {
			background-color: $black;
		}
	}
	&__map {
		width: calc(100% - 72px);
		position: relative;
		img {
			width: 100%;
			display: flex;
		}
	}
	&__map-wrap {
		position: absolute;
		bottom: 24px;
		left: 41px;
	}
	&__img {
		position: relative;
		z-index: 1;
	}
	&__dog-text {
		position: absolute;
		right: -162px;
		top: -120px;
		background-color: $white;
		padding: 28px 25px;
		border-radius: 15px;
		box-shadow: 4px 3px 10px rgba(0, 0, 0, 0.19);
		text-align: center;
		z-index: 4;
		&::before {
			content: ' ';
			position: absolute;
			width: 0;
			height: 0;
			left: 15px;
			bottom: -21px;
			border: 12px solid;
			border-color: $white transparent transparent $white;
			transform: skewX(-20deg);
			z-index: 5;
		}
		&::after {
			content: ' ';
			position: absolute;
			width: 0;
			height: 0;
			left: 13px;
			bottom: -23px;
			border: 8px solid;
			border-color: rgba($black, 0.3) transparent transparent rgba($black, 0.3);
			transform: skewX(-20deg);
			z-index: -1;
			-webkit-filter: blur(5px);
			-moz-filter: blur(5px);
			-o-filter: blur(5px);
			-ms-filter: blur(5px);
			filter: blur(5px);
		}
	}
	&__desc {
		color: #9c9c9c;
		font-family: $font-ralB;
		font-size: 15px;
		font-weight: 500;
		line-height: 17.92px;
		margin-bottom: 25px;
	}
	&__btn {
		padding: 9px 29px 11px 28px;
		border: 2px solid $green;
		color: #636363;
		font-family: $font-ralB;
		font-size: 15px;
		font-weight: 500;
		line-height: 17.92px;
		border-radius: 50px;
		text-decoration: none;
		transition: all 0.4s;
		&:hover {
			background-color: $green;
			color: $white;
		}
	}
	&__text {
		width: calc(100% - 72px);
		background-color: $white;
		display: flex;
		padding: 36px 0 48px;
		justify-content: space-between;
	}
	&__par {
		color: #7b7b7b;
		font-family: $font-roboto;
		font-size: 13px;
		font-weight: 300;
		line-height: 24px;
		&:first-child {
			margin-left: 27px;
		}
		&:last-child {
			margin-right: 104px;
		}
		a {
			color: #7b7b7b;
			font-family: $font-roboto;
			font-size: 13px;
			font-weight: 300;
			text-decoration: none;
			transition: color 0.4s;
			&:hover {
				color: $green;
			}
		}
	}
	&__pop {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100vh;
		opacity: 0;
		z-index: -1;
		transition: all 0.4s;
		img {
			width: 100%;
			height: 100%;
		}
	}
}
.contact__pop.contact__pop--active {
	opacity: 1;
	z-index: 110;
}
.contact__google {
	width: 100%;
	height: 481px;
}
.contact__map-img {
	height: 481px;
}