.values {
	padding-top: 90px;
	padding-bottom: 147px;
	&__wrap {
		position: relative;
	}
	&__list {
		display: flex;
		margin-top: 90px;
		li {
			height: 469px;
			width: 16.65%;
			overflow: hidden;
			position: relative;
			transition: width 0.4s;
			&:hover {
				width: 25%;
			}
		}
	}
	&__item {
		use {
			fill: $green;
		}
	}
	&__content {
		position: absolute;
		top: 44px;
		left: 31px;
	}
	&__svg {
		width: 40px;
		height: 47px;
		margin-bottom: 27px;
	}
	&__text {
		color: $green;
		font-family: $font-ralB;
		font-size: 14px;
		font-weight: 700;
		line-height: 24px;
		letter-spacing: 0.49px;
	}
	&__box {
		position: absolute;
		bottom: -44px;
		left: 50%;
		transform: translateX(-50%);
		box-shadow: 0 0 43px rgba(0, 0, 0, 0.17);
		background-color: $white;
		padding: 35px 90px 42px;
	}
	&__subtitle {
		color: #423232;
		font-family: $font-ralB;
		font-size: 24px;
		font-weight: 700;
		letter-spacing: 0.84px;
		text-align: center;
	}
	&__par {
		color: #423232;
		font-family: $font-ral;
		font-size: 14px;
		font-weight: 400;
		line-height: 24px;
		letter-spacing: 0.49px;
		min-width: 574px;
		text-align: center;
		margin-top: 24px;
	}
}