.overflow-y-hidden {
	overflow-y: hidden;
}
.btn {
	border-radius: 50px;
	border: 2px solid rgba($white, 0.35);
	font-family: $font-robotoL;
	font-weight: 300;
	color: $whiteO;
	font-size: 13px;
	letter-spacing: 0.33px;
	padding: 12px 28px;
	cursor: pointer;
	text-decoration: none;
	position: relative;
	z-index: 3;
	transition: background-color 0.4s;
	&:hover {
		background-color: rgba($green, 0.23);
	}
}
.title {
	color: #848484;
	font-family: $font-ralB;
	font-size: 30px;
	font-weight: 700;
	text-transform: uppercase;
	letter-spacing: 3px;
	text-align: center;
}
.descr {
	color: #848484;
	font-family: $font-ral;
	font-size: 24px;
	font-weight: 400;
	letter-spacing: 0.84px;
	text-align: center;
	margin-top: 20px;
}
.form {
	opacity: 0;
	visibility: hidden;
	label {
		color: #444444;
		font-family: $font-ralM !important;
		font-size: 14px !important;
		font-weight: 500;
		letter-spacing: 0.54px;
		input {
			box-shadow: 1px 3px 13px rgba(140, 140, 140, 0.19);
			border-radius: 50px;
			background-color: #ffffff;
			border: transparent;
			box-sizing: border-box;
			padding: 15px 25px 18px 25px;
			width: 227px;
			color: $black;
			font-family: $font-ralM;
			font-size: 16px;
			font-weight: 500;
			letter-spacing: 0.62px;
			text-align: center;
			margin-top: 20px;
			margin-right: 23px;
			outline: none;
			&::-webkit-input-placeholder {color:#d3d3d3;}
			&::-moz-placeholder          {color:#d3d3d3;}/* Firefox 19+ */
			&:-moz-placeholder           {color:#d3d3d3;}/* Firefox 18- */
			&:-ms-input-placeholder      {color:#d3d3d3;}
		}
		.form__phone {
			padding: 15px 18px 18px 44px;
			text-align: left;
			&::-webkit-input-placeholder {text-align: left}
			&::-moz-placeholder          {text-align: left}/* Firefox 19+ */
			&:-moz-placeholder           {text-align: left}/* Firefox 18- */
			&:-ms-input-placeholder      {text-align: left}
		}
	}
	&__block {
		display: flex;
		justify-content: center;
		align-items: flex-end;
		position: relative;
		margin-bottom: 29px;
	}
	&__label {
		display: flex;
		flex-direction: column;
		position: relative;
	}
	&__text {
		margin-right: 23px;
		position: relative;
		&::after {
			content: '';
			position: absolute;
			bottom: -29px;
			right: 11px;
			width: 11px;
			height: 11px;
			border-radius: 50%;
		}
	}
	&__btn {
		box-shadow: 1px 3px 13px rgba(0, 0, 0, 0.19);
		border-radius: 50px;
		background-color: $greenL;
		color: $white;
		font-family: $font-ralB;
		font-size: 16px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 0.62px;
		padding: 14.5px 46px 14.5px 47px;
		box-sizing: border-box;
		min-width: 227px;
		cursor: pointer;
		outline: none;
		transition: background-color 0.4s, border 0.4s;
		border: 2px solid $greenL;
		text-decoration: none;
		&:hover {
			background: $green;
			border: 2px solid $green;
			box-sizing: border-box;
		}
	}
	&__error {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		text-align: center;
		opacity: 0;
		visibility: hidden;
	}
	&__ok {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		text-align: center;
		opacity: 0;
		visibility: hidden;
	}
	&__warn {
		color: $white;
		font-family: $font-ral;
		font-size: 24px;
		font-weight: 400;
		letter-spacing: 2.4px;
		margin-bottom: 80px;
	}
	&--active {
		opacity: 1;
		visibility: visible;
	}
}
.checkbox__label {
	display: flex;
	justify-content: center;
}
.checkbox {
	position: absolute;
	z-index: -1;
	opacity: 0;
	margin: 10px 0 0 20px;
}
.checkbox__text {
	position: relative;
	padding: 3px 0 0 37px;
	cursor: pointer;
}
.checkbox__text::before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 22px;
	height: 22px;
	border-radius: 50%;
	background-color: $gray;
	transition: .2s;
}
.checkbox__text::after {
	content: '';
	position: absolute;
	top: 4px;
	left: 3px;
	width: 16px;
	height: 12px;
	transition: .2s;
	background: transparent;
}
.checkbox:checked + .checkbox__text::after {
	background: url("../images/chekbox.png") center no-repeat;
}
.checkbox:checked + .checkbox__text::before {
	background-color: $greenL;
}
.burger {
	width: 45px;
	height: 45px;
	position: fixed;
	left: 90px;
	bottom: 30px;
	cursor: pointer;
	opacity: 0;
	z-index: 0;
	transition: opacity 0.4s;
	&--active {
		opacity: 1;
		z-index: 11;
	}
	&::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: $green;
		opacity: 0.5;
		border-radius: 50%;
		z-index: 10;
		transition: opacity 0.4s;
	}
	&::before {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		width: 20px;
		height: 2px;
		background-color: $white;
		transform: translate(-50%, -50%);
		z-index: 15;
	}
	span {
		&::after {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			width: 20px;
			height: 2px;
			background-color: $white;
			transform: translate(-50%, -6px);
			z-index: 15;
			transition: all 0.2s;
		}
		&::before {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			width: 20px;
			height: 2px;
			background-color: $white;
			transform: translate(-50%, 4px);
			z-index: 15;
			transition: all 0.2s;
		}
	}
	&:hover {
		&::after {
			opacity: 1;
		}
	}
	&--close {
		&::before {
			opacity: 0;
		}
		span {
			&::before {
				transform: rotate(45deg) translate(-1px, -1px);
				width: 15px;
				top: 50%;
				left: 10.5px;
			}
			&::after {
				transform: rotate(-45deg) translate(1px, -1px);
				width: 15px;
				top: 50%;
				left: 19.5px;
			}
		}
	}
}
.arrow-up {
	width: 45px;
	height: 45px;
	position: fixed;
	left: 30px;
	bottom: 30px;
	cursor: pointer;
	opacity: 0;
	z-index: 0;
	transition: opacity 0.4s;
	&::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: $green;
		opacity: 0.5;
		border-radius: 50%;
		z-index: 11;
		transition: opacity 0.4s;
	}
	&:hover {
		&::after {
			opacity: 1;
		}
	}
	&--active {
		z-index: 10;
		opacity: 1;
	}
}
.arrow {
	position: relative;
	width: 100%;
	height: 100%;
	&::after {
		position: absolute;
		content: '';
		top: 50%;
		left: 60%;
		height: 2px;
		width: 15px;
		background-color: $white;
		transform: translate(-50%, -50%) rotate(45deg);
		z-index: 15;
	}
	&::before {
		position: absolute;
		content: '';
		top: 50%;
		left: 40%;
		height: 2px;
		width: 15px;
		background-color: $white;
		transform: translate(-50%, -50%) rotate(-45deg);
		z-index: 15;
	}
}
input.error {
	&::-webkit-input-placeholder {color:#d60f0f;}
	&::-moz-placeholder          {color:#d60f0f;}/* Firefox 19+ */
	&:-moz-placeholder           {color:#d60f0f;}/* Firefox 18- */
	&:-ms-input-placeholder      {color:#d60f0f;}
}
.form__text.error {
	&::after {
		background-color: #d60f0f;
	}
}
.form__text.valid {
	&::after {
		background-color: $greenL;
	}
}
.checkbox__text.error {
	&::before {
		background-color: #d60f0f;
	}
}
.bottom-menu {
	position: fixed;
	bottom: -100%;
	left: 0;
	width: 100%;
	background-color: rgba(#1e3924, 0.5);
	z-index: 9;
	transition: bottom 0.6s;
	ul {
		display: flex;
		justify-content: space-between;
		height: 96px;
		align-items: center;
		margin-left: 10%;
		margin-right: 10%;
	}
	&__item {
		text-decoration: none;
		color: $white;
		transition: color 0.4s;
		&:hover {
			color: $greenL;
		}
	}
	&--active {
		bottom: 0;
	}
}