.factor {
	padding-top: 90px;
	padding-bottom: 99px;
	&__wrap {
		margin-left: 10%;
		margin-right: 10%;
	}
	&__block {
		display: flex;
		margin-top: 88px;
		margin-bottom: 93px;
	}
	&__title {
		text-align: center;
		color: $green;
		font-family: $font-ralB;
		font-size: 30px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 3px;
	}
	&__descr {
		text-align: center;
		color: $green;
		font-family: $font-ralB;
		font-size: 24px;
		font-weight: 700;
		letter-spacing: 1.21px;
		margin-top: 20px;
	}
	&__subtitle {
		color: #242424;
		font-family: $font-ralB;
		font-size: 24px;
		font-weight: 700;
		line-height: 36px;
		letter-spacing: 0.84px;
	}
	&__box {
		width: 50%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	&__img {
		img:first-child {
			margin-right: 33px;
		}
	}
	&__video {
		width: 50%;
	}
	&__btn {
		text-align: center;
		a {
			border: 3px solid $green;
			color: $green;
			font-family: $font-ralB;
			font-size: 16px;
			font-weight: 700;
			line-height: 31.85px;
			text-transform: uppercase;
			letter-spacing: 0.6px;
			transition: color 0.3s, background-color 0.3s;
			padding: 14px 27px;
			&:hover {
				background-color: $green;
				color: $white;
			}
		}
	}
}