.header {
	width: 100%;
	height: 100vh;
	position: relative;
	overflow: hidden;
	z-index: 55;
	&::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		z-index: 0;
		background: url(../images/cloud/cloud-top.png) no-repeat;
		width: 100%;
		height: 100%;
	}
	&__wrap {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: url("../images/header/header-bg.jpg") no-repeat center;
		-webkit-background-size: cover;
		background-size: cover;
		z-index: -1;
		transition: all 1s;
		&--skale {
		transform: scale(1.3);
		-ms-transform: scale(1.3);
		-webkit-transform: scale(1.3);
		-o-transform: scale(1.3);
		-moz-transform: scale(1.3);
		}
	}
	&__box {
		text-align: center;
		margin-top: 5.1%;
		position: relative;
		z-index: 1;
	}
	&__logo {
		svg {
			height: 126px;
		}
		use {
			fill: $white;
		}
	}
	&__title {
		text-shadow: 4px 6px 43px rgba(57, 58, 63, 0.54);
		color: $white;
		font-family: $font-ralB;
		font-size: 56px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 3px;
		margin-top: 2.7%;
	}
	&__descr {
		color: $white;
		font-family: $font-ral;
		font-size: 18px;
		font-weight: 400;
		letter-spacing: 1.35px;
		margin-top: 15px;
		line-height: 28px;
	}
	&__price {
		color: $white;
		font-family: $font-ralB;
		font-size: 18px;
		font-weight: 700;
		letter-spacing: 1.35px;
		margin-top: 10px;
	}
	&__btn {
		margin-top: 7%;
		cursor: pointer;
		a {
			text-transform: uppercase;
			text-shadow: 3px 5px 18px $black;
			color: $white;
			font-family: $font-ralB;
			font-size: 16px;
			font-weight: 700;
			letter-spacing: 1.29px;
			overflow: hidden;
			position: relative;
			z-index: 1;
			cursor: pointer;
			padding: 18px 40px;
			&::after {
				content: '';
				z-index: -1;
				position: absolute;
				border-radius: 50px;
				background: url("../images/header/header-bg.jpg") center no-repeat;
				top: 0;
				left: 0;
				height: 100%;
				width: 100%;
				filter: blur(5px);
				transition: opacity 0.4s;
			}
			&:hover {
				&::after {
					opacity: 0;
				}
			}
		}
	}
	&--firefox {
		top: -10%;
		left: -10%;
		width: 120%;
		height: 120%;
	}
}