.structure {
	padding-top: 100px;
	padding-bottom: 79px;
	&__title {
		color: #848484;
		font-family: $font-ralB;
		font-size: 30px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 3px;
		text-align: center;
	}
	&__descr {
		color: #848484;
		font-family: $font-ral;
		font-size: 24px;
		font-weight: 400;
		letter-spacing: 0.84px;
		text-align: center;
		margin-top: 20px;
	}
	&__wrap {
		margin-top: 118px;
	}
	&__list {
		display: flex;
		flex-flow: wrap;
	}
	&__item {
		display: flex;
		width: 33.3%;
		margin-bottom: 84px;
	}
	&__img {
		width: 91px;
		height: 91px;
		background-color: #b6d841;
		display: flex;
		justify-content: center;
		align-items: center;
		use {
			fill: $white;
		}
	}
	&__text {
		color: #9f9f9f;
		font-family: $font-ralB;
		font-size: 14px;
		font-weight: 700;
		line-height: 21px;
		letter-spacing: 0.49px;
		margin-left: 27px;
		display: flex;
		align-items: center;
	}
}