.popup {
	width: 100%;
	height: 100vh;
	overflow: hidden;
	z-index: -1;
	background: url("../images/header/header-bg.jpg") center no-repeat;
	-webkit-background-size: cover;
	background-size: cover;
	position: fixed;
	top: 0;
	left: 0;
	opacity: 0;
	transition: opacity 0.4s;
	&--active {
		opacity: 1;
		z-index: 110;
	}
	&::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		z-index: 0;
		background: url(../images/cloud/cloud-top.png) no-repeat;
		width: 100%;
		height: 100%;
	}
	&__wrap {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 100%;
		position: relative;
		z-index: 8;
	}
	&__close {
		opacity: 0.39;
		color: $white;
		font-family: $font-ralB;
		font-size: 22px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 1.16px;
		position: absolute;
		right: 53px;
		top: 57px;
		text-decoration: none;
		cursor: pointer;
		z-index: 8;
		transition: opacity 0.4s;
		&:hover {
			opacity: 0.9;
		}
	}
	&__box {
		background: url("../images/cloud/cloud-form.png") center no-repeat;
		width: 1072px;
		height: 559px;
		text-align: center;
		position: relative;
	}
	&__title {
		text-shadow: 8px 12px 29px rgba(57, 58, 63, 0.69);
		color: $white;
		font-family: $font-ralB;
		font-size: 45px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 1.16px;
		text-align: center;
		margin-top: 85px;
		margin-bottom: 82px;
	}
}