.ipoteka {
	padding-top: 113px;
	padding-bottom: 200px;
	background: url("../images/ipoteka/ipoteka-bg.jpg") center no-repeat;
	-webkit-background-size: cover;
	background-size: cover;
	&__title {
		color: $black;
	}
	&__descr {
		color: $black;
	}
	&__wrap {
		position: relative;
		margin-left: 10%;
		margin-right: 10%;
		height: 665px;
	}
	&__box {
		position: absolute;
		top: 0;
		left: 0;
		display: flex;
		margin-top: 53px;
		justify-content: space-between;
		width: 100%;
	}
	&__item {
		width: 49%;
	}
	&__bank {
		text-align: center;
		background-color: $white;
		padding-top: 54px;
		padding-bottom: 46px;
		margin-bottom: 10px;
	}
	&__subtitle {
		background-color: $white;
		padding: 42px 40px 40px 57px;
		color: #7d7d7d;
		font-family: $font-ralB;
		font-size: 14px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 0.7px;
		cursor: pointer;
		position: relative;
		transition: color 0.4s;
		&::before {
			content: '';
			position: absolute;
			width: 32px;
			height: 6px;
			background-color: #d2d2d2;
			right: 47px;
			top: 50%;
			transform: translateY(-50%);
			transition: all 0.4s;
		}
		&::after {
			content: '';
			position: absolute;
			height: 32px;
			width: 6px;
			background-color: #d2d2d2;
			right: 60px;
			top: 50%;
			transform: translateY(-50%);
			transition: all 0.4s;
		}
		&:hover {
			color: $green;
			&::before {
				background-color: $green;
			}
			&::after {
				background-color: $green;
			}
		}
		&--active {
			color: $green;
			&::before {
				background-color: $green;
			}
			&::after {
				background-color: $green;
				transform: translateY(-50%) rotate(-90deg);
			}
		}
	}
	&__text {
		background-color: #f8f8f8;
		padding: 42px 40px 40px 57px;
		display: none;
		&--active {
			display: block;
		}
	}
	&__par {
		color: #4d4d4d;
		font-family: $font-ralM;
		font-size: 13px;
		font-weight: 500;
		line-height: 18px;
		letter-spacing: 0.4px;
		margin-bottom: 35px;
	}
	&__list {
		display: flex;
		li {
			width: 25%;
			display: flex;
			flex-direction: column;
			span {
				color: #595959;
				font-size: 13px;
				font-weight: 400;
				font-family: $font-ral;
			}
		}
	}
	&__svg {
		height: 55px;
	}
	&__tab {
		margin-top: 6px;
	}
}