// stylelint-disable scss/dollar-variable-empty-line-before

// COLORS

$black: #000;
$white: #fff;
$whiteO: #fefefe;
$green: #659c4b;
$gray: #9b9b9b;
$greenL: #b6d841;

// FONTS

$font-roboto: 'Roboto', 'Arial', sans-serif;
$font-robotoB: 'RobotoBold', 'Arial', sans-serif;
$font-robotoL: 'RobotoLight', 'Arial', sans-serif;
$font-ral: 'Raleway', 'Arial', sans-serif;
$font-ralM: 'RalewayMedium', 'Arial', sans-serif;
$font-ralB: 'RalewayBold', 'Arial', sans-serif;


// breakpoints

$sm: 576px;
$md: 768px;
$lg: 992px;
$xl: 1200px;

