.future {
	background: url("../images/future/future-bg.jpg") center no-repeat;
	-webkit-background-size: cover;
	background-size: cover;
	padding-top: 52px;
	padding-bottom: 120px;
	position: relative;
	width: 100%;
	&__logo {
		text-align: center;
		position: relative;
		z-index: 2;
		use {
			fill: $white;
		}
	}
	&__title {
		text-align: center;
		color: $whiteO;
		font-family: $font-ralB;
		font-size: 1.8vw;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 3px;
		margin-top: 40px;
		position: relative;
		z-index: 2;
	}
	&::before {
		content: url("../images/future/future-hous.png");
		background: url("../images/future/future-hous.png") right no-repeat;
		position: absolute;
		right: 0;
		bottom: 0;
	}
}
.photo {
	&__list {
		display: flex;
		flex-flow: wrap;
		li {
			width: 20%;
			min-height: 250px;
			position: relative;
			overflow: hidden;
		}
	}
	&__item {
		width: 100%;
		height: 100%;
		//max-height: 377px;
		transition: all 0.6s;
		img {
			height: 100%;
			width: 100%;
			min-height: 250px;
			display: block;
		}
		&:hover {
			transform: scale(1.2);
		}
	}
}