.hous {
	background: url("../images/green-bg.jpg") center no-repeat;
	-webkit-background-size: cover;
	background-size: cover;
	position: relative;
	max-height: 965px;
	&__block {

	}
	&__wrap {
		margin-left: 10%;
		margin-right: 10%;
		transform: translateY(-250px);
	}
	&__box {
		display: flex;
		width: 100%;
	}
	&__list {
		width: 100%;
		display: flex;
		text-align: center;
	}
	&__tabs {
		color: #9b9b9b;
		font-family: $font-ralB;
		font-size: 30px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 3px;
		text-decoration: none;
		cursor: pointer;
		width: 50%;
		padding: 41px 0;
		margin-bottom: 28px;
		transition: background-color 0.4s, box-shadow 0.4s;
		&--active {
			box-shadow: 7px 16px 28px rgba(0, 0, 0, 0.2);
			background-color: #ffffff;
		}
	}
	&__tab-wrap {
		width: 100%;
		height: 100%;
		position: relative;
		min-height: 703px;
	}
	&__tab {
		display: flex;
		flex-flow: wrap;
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		opacity: 0;
		width: 100%;
		height: 100%;
		transition: opacity 0.4s;
		&--active {
			opacity: 1;
			z-index: 1;
		}
	}
	&__item {
		position: relative;
		display: flex;
		width: 50%;
		cursor: pointer;
		z-index: 1;
		&::after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-image: linear-gradient(to top, #2a1e17 0%, rgba(63, 49, 48, 0) 49%, rgba(64, 50, 50, 0) 52%);
			z-index: 0;
			transition: background-color 0.4s, opacity 0.4s;
		}
		&:hover {
			&::after {
				background-color: #2a1e17;
				opacity: 0.64;
			}
			.hous__arrow {
				bottom: auto;
				top: 24px;
			}
			.hous__size {
				opacity: 0;
			}
			.hous__text {
				opacity: 1;
			}
		}
		img {
			width: 100%;
			height: 340px;
		}
	}
	&__text {
		opacity: 0;
		position: absolute;
		z-index: 1;
		padding-top: 62px;
		padding-left: 84px;
		transition: opacity 0.4s;
	}
	&__size {
		position: absolute;
		bottom: 39px;
		left: 0;
		text-align: center;
		color: $white;
		font-family: $font-ralB;
		font-size: 23px;
		font-weight: 700;
		letter-spacing: 3px;
		width: 100%;
		z-index: 1;
		transition: opacity 0.2s;
	}
	&__arrow {
		position: absolute;
		bottom: 16px;
		left: 50%;
		transform: translateX(-50%);
		background: url("../images/hous/arrow.png") center no-repeat;
		width: 50px;
		height: 11px;
		z-index: 1;
		transition: bottom 1s, top 1s;
	}
	&__title {
		margin-top: 89px;
		margin-bottom: 69px;
		color: $white;
	}
	.form__text {
		color: $white;
		text-align: center;
	}
	.form__agreement {
		text-align: center;
	}
	&__subtitle {
		color: $white;
		font-family: $font-ralB;
		font-size: 24px;
		font-weight: 700;
		letter-spacing: 2.4px;
		margin-bottom: 19px;
	}
	&__size--two {
		color: $white;
		font-family: $font-ralB;
		font-size: 30px;
		font-weight: 700;
		letter-spacing: 3px;
		margin-bottom: 31px;
	}
	&__par {
		color: $white;
		font-family: $font-ralM;
		font-size: 14px;
		font-weight: 500;
		line-height: 24px;
		letter-spacing: 1.4px;
		&--top {
			margin-bottom: 29px;
		}
	}
	.checkbox__text {
		color: $white;
	}
	&__form {
		position: relative;
	}
}
.pop {
	position: absolute;
	top: 0;
	left: 0;
	width: calc(100% - 60px);
	height: calc(100% - 95px);
	background-color: $white;
	z-index: 0;
	padding: 37px 28px 58px 32px;
	box-shadow: 13px 11px 51px rgba(0, 0, 0, 0.25);
	opacity: 0;
	transition: opacity 0.4s;
	&__title {
		color: #424242;
		font-family: $font-ralB;
		font-size: 30px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 1.5px;
		text-align: center;
		margin-bottom: 34px;
	}
	&__box {
		display: flex;
		height: calc(100% - 60px);
	}
	&__slider {
		width: 65%;
	}
	&__wrap {
		width: 35%;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
		margin-left: 57px;
	}
	&__text {
		max-height: 550px;
		overflow-y: scroll;
	}
	&__subtitle {
		color: #424242;
		font-family: $font-ralB;
		font-size: 18px;
		font-weight: 700;
		letter-spacing: 0.9px;
		margin-bottom: 34px;
	}
	&__block {
		display: flex;
		flex-flow: wrap;
	}
	&__info {
		width: 50%;
		margin-bottom: 35px;
	}
	&__desc {
		color: #858585;
		font-family: $font-ral;
		font-size: 18px;
		font-weight: 400;
		letter-spacing: 0.9px;
		margin-bottom: 19px;
	}
	&__par {
		color: #424242;
		font-family: $font-ral;
		font-size: 14px;
		font-weight: 400;
		line-height: 24px;
		letter-spacing: 0.7px;
		padding-right: 15px;
	}
	&__btn {
		display: block;
		margin-top: 26px;
		text-decoration: none;
	}
	&__for {
		height: calc(100% - 157px);
		.slick-track {
			height: 100%;
		}
		.slick-list {
			width: 100% !important;
			height: 100% !important;
		}
		.slick-prev {
			left: 25px;
			z-index: 1;
		}
		.slick-next {
			right: 35px;
			z-index: 1;
		}
		.slick-prev::before, .slick-next::before {
			font-size: 35px;
		}
		.slick-prev, .slick-next {
			width: 40px;
			height: 40px;
		}
	}
	&__nav {
		display: flex;
	}
	&__close {
		position: absolute;
		top: 37px;
		right: 28px;
		width: 28px;
		height: 28px;
		cursor: pointer;
		&::after {
			position: absolute;
			content: '';
			width: 28px;
			height: 2px;
			background-color: #d3d3d3;
			top: 50%;
			left: 0;
			transform: rotate(45deg);
			transition: background-color 0.4s;
		}
		&::before {
			position: absolute;
			content: '';
			width: 28px;
			height: 2px;
			background-color: #d3d3d3;
			top: 50%;
			left: 0;
			transform: rotate(-45deg);
			transition: background-color 0.4s;
		}
		&:hover {
			&::after, &::before {
				background-color: $green;
			}
		}
	}
	&--active {
		z-index: 7;
		opacity: 1;
	}
}
.nav-item {
	width: 33%;
	height: 157px;
	img {
		width: 100%;
		height: 100%;
		object-fit: contain;
	}
}
.for-item {
	height: 100% !important;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	img {
		width: 100%;
		height: 100%;
		object-fit: contain;
	}
}
.pop__slider {
	.pop__for .slick-prev, .pop__for .slick-next {
		border-radius: 50%;
		background-color: rgba($green, 0.7);
		box-shadow: 1px 3px 13px rgba(0, 0, 0, 0.19);
		transition: background-color 0.4s;
		&:hover {
			background-color: rgba($green, 1);
		}
	}
	.slick-prev::before, .slick-next::before {
		content: '';
		position: absolute;
		top: 60%;
		left: 50%;
		height: 2px;
		width: 15px;
		background-color: $white;
		opacity: 1;

	}
	.slick-prev::after, .slick-next::after {
		content: '';
		position: absolute;
		top: 40%;
		left: 50%;
		height: 2px;
		width: 15px;
		background-color: $white;
		opacity: 1;
	}
	.slick-prev::before {
		transform: translate(-60%, -25%) rotate(45deg);
	}
	.slick-prev::after {
		transform: translate(-60%, -85%) rotate(-45deg);
	}
	.slick-next::before {
		transform: translate(-40%, -25%) rotate(-45deg);
	}
	.slick-next::after {
		transform: translate(-40%, -85%) rotate(45deg);
	}
}